import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import style from "../PlateformUser/PlateformUser.module.css";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import BorderBottom from "../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
const InstituteStaffCard = ({
  staff,
  selectedId,
  onSelect,
  selectList,
  isMultiSelect,
  onMultiSelect,
}) => {
  const { t } = useTranslation();
  const onSelectStaff = () => {
    onSelect(staff);
  };
  const onSelectMultipleStaff = () => {
    onMultiSelect(staff);
  };
  return (
    <>
      {isMultiSelect ? (
        <div
          className={style.assign_show_list_each}
          onClick={onSelectMultipleStaff}
        >
          {selectList?.includes(staff?._id) ? (
            <img
              src={`${assestsUserAdmissionUrl}/check.svg`}
              alt="icon"
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
            />
          ) : (
            <img
              style={{
                height: "1.3rem",
                borderRadius: "0",
                cursor: "pointer",
              }}
              src={`${assestsUserAdmissionUrl}/uncheck.svg`}
              alt="icon"
            />
          )}

          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-staff-avatar.svg`
            }
            alt="profile avatar"
          />
          <div className={style.assign_each_paragraph}>
            <h6>{`${staff?.staffFirstName ?? ""} ${
              staff?.staffMiddleName ?? ""
            } ${staff?.staffLastName ?? ""}`}</h6>
            <p>
              {t("index_number")} {" : "}
              {staff?.staffROLLNO}
            </p>
          </div>
        </div>
      ) : (
        <div
          className={style.assign_show_list_each}
          style={
            selectedId === staff?._id
              ? { backgroundColor: "rgba(75, 26, 133, 0.1)" }
              : null
          }
          onClick={onSelectStaff}
        >
          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-staff-avatar.svg`
            }
            alt="profile avatar"
          />
          <div className={style.assign_each_paragraph}>
            <h6>{`${staff?.staffFirstName ?? ""} ${
              staff?.staffMiddleName ?? ""
            } ${staff?.staffLastName ?? ""}`}</h6>
            <p>
              {t("index_number")} {" : "}
              {staff?.staffROLLNO}
            </p>
          </div>
        </div>
      )}
      <BorderBottom
        customStyleBorder={{
          margin: "0",
        }}
      />
    </>
  );
};

export default InstituteStaffCard;
