import style from "../PlateformUser/PlateformUser.module.css";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../Utils/Functions/nextPage";
import { customDebounce } from "../../Utils/Functions/debounce";
import { useInstituteAllStaff } from "../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import InstituteStaffCard from "./InstituteStaffCard";
import QvipleLoader from "../../Loader/QvipleLoader";
import Loading from "../../Loader/Loading";
import EmptyMessage from "../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../Ui/Button/CreateButton";
const InstituteStaff = ({
  title,
  onClose,
  selectedId,
  onSelectStaff,
  instituteId,
  isMultiSelect,
  onMultiSelect,
  selectList,
  onDone,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { instituteAllStaff, instituteAllStaffLoading } = useInstituteAllStaff({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (instituteAllStaff?.list?.length) {
      let obj = nextPage(instituteAllStaff?.list?.length);
      setState(() => obj);
    }
  }, [instituteAllStaff?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onSelect = (value) => {
    if (selectedId === value?._id) {
    } else {
      onSelectStaff(value);
    }
  };
  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t(title ?? "staff_list")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              {isMultiSelect && (
                <CreateButton
                  label={t("select")}
                  count={selectList?.length}
                  onAction={onDone}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              )}
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {instituteAllStaff?.list?.map((staff, index) =>
            instituteAllStaff?.list?.length === index + 1 ? (
              <div key={staff?._id} ref={ref}>
                <InstituteStaffCard
                  staff={staff}
                  selectedId={selectedId}
                  onSelect={onSelect}
                  selectList={selectList}
                  isMultiSelect={isMultiSelect}
                  onMultiSelect={onMultiSelect}
                />
              </div>
            ) : (
              <InstituteStaffCard
                staff={staff}
                key={staff?._id}
                selectedId={selectedId}
                onSelect={onSelect}
                selectList={selectList}
                isMultiSelect={isMultiSelect}
                onMultiSelect={onMultiSelect}
              />
            )
          )}
          {instituteAllStaffLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            instituteAllStaff?.list?.length === 0 && (
              <EmptyMessage
                title="No staff found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            instituteAllStaff?.list?.length === 0 && (
              <EmptyMessage
                title="No staff."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default InstituteStaff;
